import * as React from 'react';
import {
  JsonML,
  getTagName,
  getAttributes,
} from '@ali/4ever-utils';
import Sticker from '../models';

export default {
  name: 'sticker',

  match(node: JsonML): boolean {
    return (
      getTagName(node) === 'span' &&
      getAttributes(node)['data-type'] === Sticker.TYPE
    );
  },

  convert: (node: JsonML): React.ReactElement => {
    const payload = getAttributes(node);
    return <span data-type="sticker">{`[${payload.code}]`}</span>;
  },
};
